/* global jQuery MktoForms2 */
/*
 * inject hidden QSC location field
 */
(function ($, MktoForms2) {

	"use strict";

	MktoForms2.onFormRender(function (form) {
		form.addHiddenFields({"qsc-location":"EMEA"});
	});

	MktoForms2.onFormRender(function () {
		$("input[name='MktoPersonNotes']").on("click", function() {
	    $(".MktoPersonNotes .mktoFieldDescriptor .mktoRequiredField .mktoError").addClass("hiderequiredmessage");
		});


	});
}(jQuery, MktoForms2));
